import React, { Component } from 'react';
import './App.css';
import TextDisplay from './components/Form/TextDisplay/TextDisplay';
import Form from './components/Form/Form';
import Modal from './components/Modal/Modal';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = { displayText: "Enter a Key", copyAlert: false };
    this.handleData = this.handleData.bind(this);
    this.checkFullScreen();
    this.copyText = this.copyText.bind(this);
  }

  handleData(data) {
    var pass = this.generatePassword();

    this.setState({
      displayText: "" + data.toUpperCase() + pass
    });
  }

  generatePassword() {
    var pass = "";
    var randChar;

    for (var i = 0; i < 3; i++) {
      pass += '-';
      for (var j = 0; j < 4; j++) {
        randChar = this.getRandomChar();
        pass += randChar;
      }
    }

    return pass;
  }

  getRandomChar() {
    var caps = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.split('');
    var lows = 'abcdefghijklmnopqrstuvwxyz'.split('');
    var nums = '01234567890'.split('');

    var charset = Math.floor(Math.random() * 3 + 1);
    var choice = "A";

    if (charset === 1) {
      choice = caps[Math.floor(Math.random() * caps.length)];
    } else if (charset === 2) {
      choice = lows[Math.floor(Math.random() * lows.length)];
    } else {
      choice = nums[Math.floor(Math.random() * nums.length)];
    }
    return choice;
  }

  showModal = (type) => {
    if (type === 'error') {
      this.setState({ eShow: true });
    } else if (type === 'copy') {
      this.setState({ pShow: true });
    }
  }

  hideModal = (type) => {
    if (type === 'error') {
      this.setState({ eShow: false });
    } else if (type === 'copy') {
      this.setState({ pShow: false });
    }
  }

  checkFullScreen() {
    var app = document.getElementsByClassName('App');
    if (window.innerWidth < 1000) {
      if (app.requestFullscreen) {
        app.requestFullscreen();
      } else if (app.mozRequestFullScreen) {
        app.mozRequestFullScreen();
      } else if (app.webkitRequestFullscreen) {
        app.webkitRequestFullscreen();
      } else if (app.msRequestFullscreen) {
        app.msRequestFullscreen();
      }
    }
  }

  copyText() {
    document.querySelector('#generated').select();
    document.execCommand('copy');
    document.querySelector('#generated');
  }

  render() {
    return (
      <div className="App">
        <Modal message="Please enter a four-digit code to generate your password." show={this.state.eShow} />
        <Modal message="Password Copied." show={this.state.pShow}/>
        <div className="hero flex-center">
          <div className="flex-center">
            <h1 className="text-light text-uppercase flex-center">Token</h1>
            <h4 className="text-light text-uppercase flex-center">A Simple, Secure Password Generator.</h4>
            <hr/>
          </div>

          <div className="flex-center">
            <Form icon="images/icons/flag.svg" length="4" width="small" placeholder="Key" changeHandler={this.handleData} value={this.state.key} showModal={this.showModal} hideModal={this.hideModal} buttonType="primary" color="darkviolet" />
          </div>
        </div>
        <div className="bottom flex-center">
          <h4 className="text-dark text-center text-uppercase" id="password-header">Your Password</h4>
          <TextDisplay text={this.state.displayText} copyText={this.copyText} showModal={this.showModal} hideModal={this.hideModal} />
        </div>
      </div>
    );
  }
}

export default App;
