import React, { Component } from 'react';
import './Form.css';
import '../../App.css';
import classNames from 'classnames';

export default class Form extends Component {
    constructor() {
        super();

        this.handleChange = this.handleChange.bind(this);
        this.submitHandler = this.submitHandler.bind(this);
        
        this.state = {
            key: '',
            error: false
        };
    }

    submitHandler(e) {
        e.preventDefault();

        if (this.state.key.length < 4 || this.state.key.length === 0) {
            this.props.showModal('error');
            this.handleError(true);
            setTimeout(function() {
                this.handleError(false);
            }.bind(this), 2500);
        } else if(this.state.key.length === 4){
            this.props.changeHandler(this.state.key);
            this.props.hideModal('error');
            this.setState({
                key: ''
            });
        }

    }

    handleChange(e) {
        this.setState({
            key: e.target.value
        });
    }

    handleError(isError) {
        this.setState({
            error: isError
        })
        if(!isError) {
            this.props.hideModal('error');
            this.setState({
                error: false
            })
        }
    }

    render() {
        return (
            <div>
                <h4 className="text-light text-uppercase">Enter a 4-Letter Key</h4>
                <form className="flex-form-center" onSubmit={this.submitHandler}>
                    <div className={classNames({"error ":this.state.error},"container")}>
                        <img className="icon-small" src={this.props.icon} alt="" />
                        <input type="text" id="key" maxLength={this.props.length} placeholder={this.props.placeholder} className={"input text-uppercase " + this.props.width} value={this.state.key} onChange={this.handleChange}/>
                    </div>
                    <div className="wide">
                        <button className={"button " + this.props.buttonType + " " + this.props.color} onClick={this.props.handleClick}>Generate</button>
                    </div>
                </form>
            </div>
        );
    }
}