import React, { Component } from 'react';
import './TextDisplay.css';
import '../../../App.css';

export default class TextDisplay extends Component {
    constructor(props) {
        super(props);

        this.state = {
            copyAlert: false
        }

        this.textCopy = this.textCopy.bind(this);
        this.handleCopy = this.handleCopy.bind(this);
    }

    textCopy() {
        this.props.showModal('copy');
        this.handleCopy(true);
        this.props.copyText();
        setTimeout(function() {
            this.handleCopy(false);
        }.bind(this), 2500);
    }

    handleCopy(copy){
        this.setState({
            copyAlert: copy
        });
        if(!copy) {
            this.props.hideModal('copy');
            this.setState({
                copyAlert: false
            });
        }
    }

    render() {
        return(
            <div className="display">
                <input type="text" className="text-dark" id="generated" value={this.props.text} onClick={this.textCopy} readOnly/>
            </div>
        );
    }
}