import React, { Component } from 'react';
import './Modal.css';
import '../../App.css';

export default class Modal extends Component {
    
    modalIsVisible(visible) {

        if (visible) {
            this.visibility = "modal visible flex-center"
        } else {
            this.visibility = "modal hidden"
        }
        return this.visibility
    }

    render() {
        return (
            <div className={this.modalIsVisible(this.props.show)}>
                <p className="text-light text-center">{this.props.message}</p>
            </div>
        );
    }
}